.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.header {
  width: 100%;
  height: 70px;
}

.logoBox {
  width: 70%;
  height: 30px;
  margin: 0 auto;
}

.logoBox>img {
  width: 140px;
  height: 100%;
  float: left;
  margin-top: 20px;
}

.me-auto {
  margin-left: 70px;
}

.col-md-4>h4 {
  font-size: 15px;
  color: #1a1a1a;
  font-weight: bold;
}

.col-md-4>p {
  font-size: 14px;
  color: #1a1a1a;
  font-weight: bold;
}

.c1 > h4 { text-decoration: none; font-size: 15px;
  color: #1a1a1a;
  font-weight: bold;
}
.c1 > p { text-decoration: none; font-size: 14px;
  color: #1a1a1a;
  font-weight: bold;}

.Box {
  width: auto;
  height: auto;
  margin: 30px auto;
  display: flex;
}

.box1 {
  width: 50%;
}

.box1>img {
  width: 100%;
}

.box2 {
  width: 50%;
}

.box2>img {
  width: 100%;
}

.mainBg {width: 2400px; height: 600px; margin: 50px auto;  background-size: cover;
  background-position: center; display: flex; justify-content: center;}

.start {
    transform: scale(0);
}
.end {
     transform: scale(1);
     transition :all 0.5s;
}

.footerbox {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.footerbox>img {
  width: 140px;
  height: 30px;
  margin-top: 100px;
  margin-bottom: 30px;
}

.footerbox2 {
  width: 200px;
  margin: 160px auto;
}

.Allbox_ {
  margin-left: 70px;
  padding-left: 110px;

}

.Allbox {display: flex;   margin: 70px auto;   width: 1300px;  border-Top: 1px solid #ddd;
}

/* detail */

