  width: 450px;
  margin-top: 50px;
  color: #1a1a1a;
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
  border-top: 1px solid #1a1a1a;
  text-align: left;
}

.pt-6 {text-align: left; 
  color: #1a1a1a;
  font-size: 20px;
  font-weight: 700;   
  line-height: 60px;
  height: 70px;  border-bottom: 1px solid #e8e8e8;}

.pt-7 {text-align: left; color: #999999;
  font-size: 10px; }
.pt-8 {text-align: left; font-size: 11px; color: #0c0c0c;
  line-height: 18px;} 

.pt-9 {color: #0d0c0c; text-align: left; font-size: 11px;   border-top: 1px solid #a29e9e; padding-top: 20px;} 

.pt-10 {text-align: right; 
  color: #1a1a1a;
  font-size: 20px;
  font-weight: 700;   
  line-height: 60px;
  height: 70px;}

.buttonbox {text-align: right;}
.buttonbox > button {width: 200px; height: auto; }

.container5 {width: 1200px; margin: 0 auto;}

.nav {width: 1200px; margin: 50px auto; margin-left: 60px;}
.nav_bar {width: 40%;}
.nav_bar1 {width: 30%;}
.nav_bar2 {width: 30%;}

.body {width: 1200px; margin: 0 auto; margin-left: 120px;}
